<template>
  <div class="about">
    <h1>This is an about page</h1>
    
    <div>
      <h1>My Weather App</h1>
      <button v-on:click="getWeatherData">Get Weather Data</button>
      <!-- <div>{{ weatherDataList }}</div> -->
    </div>
    <div class="divider">
      <!-- {{ weatherforcast.toString() }} -->
    </div>

    <div
      v-for="weatherData in weatherforcast"
      :key="weatherData.id"
      class="weather-data"
    >
      <div class="weather-stats">
        <div>
          <span>{{ weatherData.date }}</span>
        </div>
        <div>
          <span class="location">{{ weatherData.temperatureC }}</span>
        </div>
      </div>
      <div class="weather-summary">
        <span>{{ weatherData.summary }}</span>
      </div>
    </div>

    <div id="header">

      <v-btn>Test</v-btn>
      
   
      <div>
        <!-- Nom du formulaire -->
        {{ formData.formName }}
      </div>
      <div>
        <!-- Raison sociale -->
        <!-- {{ formData.client.raisonSociale }} -->
      </div>
      <div>
        <!-- Adresse du client -->
        <!-- {{ formData.client.adresse.rue }}
        {{ formData.client.adresse.numero }} -->

        <input
          type="text"
          v-bind="formData.client.civilite"
          placeholder="Civilite"
        />
        <input type="text" v-bind="formData.client.name" placeholder="Name" />
        <input
          type="text"
          v-bind="formData.client.last"
          placeholder="LastName"
        />

        {{ fullName }}
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Home",
  data() {
    return {
      weatherforcast: null,
      formData: {
        client: {
          adresse: {}
        },
      }
    };
  },
  computed: {
    fullName() {
      return `${this.formData.client.civilite} ${this.formData.client.name} ${this.formData.client.lastName}`;
    },
  },
  mounted() {
    axios.get(`https://localhost:44335/weatherforecast`).then((response) => {
      // JSON responses are automatically parsed.
      this.weatherforcast = response.data;

      //let response = this.api.get("https://localhost:44335/form?id=1231");
      /**
 * { 
      formName : 'BAR-4465', 
      client : { 
        raisonSociale : "toto",
        adresse : { 
          rue : "rue des champs", 
          numero : 12 
        } 
      } 
    }
 * 
 * 
 */
      this.formData = response.data;
    });
  },
  methods:{
    getWeatherData(){

    }
  }
};
</script>
